import { API_HOST } from "../../../global/constants";
import axios from "axios";

export const reducer = async (state, action) => {
  // 
  const colName = "stockBasic";
  // 
  const row = action.payload?.row;
  const index = action.payload?.index;

  // 
  switch (action.type) {
    // 
    case "LOAD":
      let url = `${API_HOST}/read.php`;
      const res = await axios.get(url, { params: { y: 2021, m: 12 } });

      console.log(res.data)

      return {
        ...state,
        data: res.data,
        loading: false,
      };

     

    // 
    case "ADD":     
      return {
        ...state,
        open: true,
        rowIndex: -1,
      };

    // 
    case "CREATE":
      const docRef = await addDoc(collection(db, colName), {
        ...row,
      });
      //  id ,  row 
      state.data.unshift({ ...row, id: docRef.id });

      return {
        ...state,
        data: state.data,
        open: false,
        rowIndex: -1,
      };

    // 
    case "EDIT":
      return { ...state, open: true, rowIndex: index };

    // 
    case "UPDATE":    

      await updateDoc(doc(db, colName, row.id), {
        ...row,
      });
      Object.assign(state.data[state.rowIndex], row);
      return {
        ...state,
        open: false,
        data: state.data,
        rowIndex: -1,
      };

    // 
    case "DELETE":      
      const id = action.payload.id;
      await deleteDoc(doc(db, colName, id));     
      const dataDel = state.data.filter((obj) => obj.id != id);

      return {
        ...state,
        data: dataDel,
        open: false,
        rowIndex: -1,
      };

    // 
    case "CLOSE":
      return { ...state, open: false };
  } // END SWITCH
};
