import React, { useState, useEffect } from 'react';
import schema from './data/schema.json';
import useAsyncReducer from './functions/asyncReducer';
import { reducer } from './data/reducer';
import TableView from './components/TableView';
import EditForm from './components/EditForm';
import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';

export default function index() {


  //   
  let { stockName, fromDate, toDate } = useParams(); 

  // 
  const initState = {
    data: [], //
    loading: true, //
    rowIndex: -1, //
    open: false, //
    search: { stockName, fromDate,toDate }, // 
    total:{}
  };

  const [state, dispatch] = useAsyncReducer(reducer, initState);

  useEffect(() => {
    // 
    dispatch({ type: 'LOAD' });
  }, []);

  let { columns } = schema;
  // 
  const defaultRow = {};
  columns.map((obj) => {
    defaultRow[obj.name] = '';
  });

  // 
  defaultRow.transDate = new Date().toISOString().substring(0, 10);

  //  row  useAsyncReducer 
  // 
  const [row, setRow] = useState(defaultRow);

  // 
  const handleAdd = () => {
    dispatch({ type: 'ADD' });
    setRow(defaultRow);
  };

  // 
  const handleEdit = (row, index) => {
    dispatch({ type: 'EDIT', payload: { index } });
    setRow(row);
    // ,, isSold 
    //  MenuItem 
    if (row.inQty) {
      setIsSold(false);
    } else {
      setIsSold(true);
    }
  };

  // 
  const [isSold, setIsSold] = useState(false);

  return (
    <div>
      <Link to="/stock-master">StockMaster</Link>
      <TableView
        columns={columns}
        state={state}
        dispatch={dispatch}
        handleEdit={handleEdit}
        handleAdd={handleAdd}        
      />
      <EditForm
        columns={columns}
        row={row}
        setRow={setRow}
        state={state}
        dispatch={dispatch}
        isSold={isSold}
        setIsSold={setIsSold}
      />
    </div>
  );
}
