import {
  readDocs,
  readDocsByDate,
  createDoc,
  updateDoc,
  deleteDoc,  
} from './firestore';

export const reducer = async (state, action) => {
  // 
  const table = 'balances';

  // 
  const row = action.payload?.row;
  const index = action.payload?.index;

  let data = state.data.slice();



  // 
  const calTotal = (data) => {
    let income = 0;
    let expense = 0;

    data.map((obj) => {
      if(obj.income)
      income +=Number(obj.income);
      if(obj.expense)
      expense += Number(obj.expense);
    });

    return {
      income,
      expense
    };
  };


  // 
  switch (action.type) {
    // 
    case "QUERY":
      // 
      const fromDate = action.payload.fromDate;
      const toDate = action.payload.toDate;
      const queryDocs = await readDocsByDate(table, fromDate, toDate)
      return {
        ...state,
        data: queryDocs,
        total: calTotal(queryDocs)
      }
    // 
    case 'LOAD':
      const docs = await readDocs(table)
      return {
        ...state,
        data: docs,
        loading: false,
        total: calTotal(docs)
      };

    // 
    case 'ADD':
      // readAndCreate()
      return {
        ...state,
        open: true,
        rowIndex: -1,
      };

    // 
    case 'CREATE':
      row.cate = '';
      const id = await createDoc(table, row);
      data.unshift({ ...row, id });
      return {
        ...state,
        data,
        open: false,
        rowIndex: -1,
      };

    // 
    case 'EDIT':
      return { ...state, open: true, rowIndex: index };

    // 
    case 'UPDATE':
      updateDoc(table, row.id, row);
      Object.assign(data[state.rowIndex], row);
      return {
        ...state,
        open: false,
        data: data,
        rowIndex: -1,
      };

    // 
    case 'DELETE':
      deleteDoc(table, row);
      return {
        ...state,
        data: state.data.filter((obj) => obj.id != row.id),
        open: false,
        rowIndex: -1,
      };

    // 
    case 'CLOSE':
      return { ...state, open: false };
  }
};
