import React, { useState, useEffect } from 'react';
import schema from './data/schema.json';
import TableView from './components/TableView';
import EditForm from './components/EditForm';
import { reducer } from './data/reducer';
import { Modal, Button } from 'semantic-ui-react';
import { useParams } from 'react-router-dom';
import MasterRow from './components/MasterRow';

export default function index() {
  // 
  // console.log(schema);

  // ()
  const getColumns = () => {
    let columns = schema.columns;
    columns = columns.filter((col) => col.viewable);
    return columns;
  };

  // 
  const columns = getColumns();

  // 
  // let { stockName } = useParams();
  let { stockName, fromDate,toDate } = useParams();

  // console.log(stockName);

  // 
  const initState = {
    table: 'detail',
    data: [],
    total: { amt: 0 },
    search: { stockName, fromDate,toDate }, //  search 
    masterData: null,
  };

  // 

  // , reducer  async function
  function useAsyncReducer(reducer, initState) {
    const [state, setState] = useState(initState),
      dispatchState = async (action) => setState(await reducer(state, action));
    return [state, dispatchState];
  }

  const [state, dispatch] = useAsyncReducer(reducer, initState);

  useEffect(() => {
    // 
    // dispatch({ type: 'LOAD_MASTER' });

    if (stockName) {
      console.log('a');
    } else {
      console.log('b');
    }
    // dispatch({ type: 'LOAD' });
    if (stockName) {
      dispatch({ type: 'LOAD_BY_NAME' });
    } else {
      dispatch({ type: 'LOAD' });
    }
  }, [stockName]);

  // 
  const defaultRow = {};

  columns.map((obj) => {
    defaultRow[obj.name] = '';
    defaultRow.stockName = stockName;
  });

  //  row  useAsyncReducer 
  // 
  const [row, setRow] = useState(defaultRow);

  const handleInputChange = (e) => {
    setRow({ ...row, [e.target.name]: e.target.value });
  };

  const handleStockChange = (e, { value }) => {
    setRow({ ...row, stockName: value });
  };

  // 
  const [isSold, setIsSold] = useState(false);

  return (
    <>
      {/* {stockName} */}
      {stockName && <MasterRow data={state.masterData}
       stockName={stockName} 
       fromDate={fromDate}
       toDate={toDate}
       />}

      <TableView
        columns={columns}
        rows={state.data}
        dispatch={dispatch}
        total={state.total}
        handleAdd={() => {
          dispatch({ type: 'ADD' });
          setRow({
            ...defaultRow,
            transDate: new Date().toISOString().substring(0, 10),
          });
        }}
        handleEdit={(row, index) => {
          dispatch({ type: 'EDIT', payload: { index } });
          setRow(row);
        }}
      />

      <Modal
        onClose={() => dispatch({ type: 'CLOSE' })}
        open={state.open}
        closeIcon
      >
        <Modal.Header></Modal.Header>
        <Modal.Content>
          <EditForm
            row={row}
            columns={columns}
            handleInputChange={handleInputChange}
            handleStockChange={handleStockChange}
            isSold={isSold}
            setIsSold={setIsSold}
          />
        </Modal.Content>
        <Modal.Actions>
          <Button
            primary
            onClick={() =>
              dispatch({
                type: state.rowIndex == -1 ? 'CREATE' : 'UPDATE',
                payload: { row },
              })
            }
          >
            
          </Button>
          <Button
            floated="left"
            color="red"
            onClick={() => dispatch({ type: 'DELETE', payload: { row } })}
          >
            
          </Button>
        </Modal.Actions>
      </Modal>
    </>
  );
}
