import React, { useState, useEffect } from 'react';
import { db } from '../../../utils/firebase';

import TableView from './components/TableView';
import EditForm from './components/EditForm';

import StockPrice from './components/StockPrice';
import GridView from './components/GridView';

export default function Index({
  statRows,
  setStatRows,
  handleRowClick,
  transactionRows,
}) {
  useEffect(() => {
    let data = cals();
    
    setStatRows(data);
  }, [transactionRows]);

  //  firebase 
  const colName = 'stockStat';

  // 
  const cals = () => {
    // setLoading(false);
    let temp = statRows.slice();
    // 
    let allCost = 0;
    let allBonus = 0;
    let allPrice = 0; //
    statRows.map((stock, index) => {
      let sum = 0;
      let sumQty = 0; //
      let sumPrice = 0; //
      let tempRows = transactionRows.filter((obj) => obj.name == stock.name);
      if (tempRows.length == 0) {
        // 
        temp[index] = {
          ...stock,
          totalCost: '',
          bouns: '',
          avgCost: '',
          qtys: '',
        };
        return;
      }

      // 
      tempRows.map((row) => {
        sum += Number(row.cost) * Number(row.qty);
        sumQty += Number(row.qty);
        sumPrice += Number(row.qty) * Number(stock.price);
      });

      // 
      const avgCost = sum / sumQty;
      // 
      const bonus = (stock.price - avgCost) * sumQty;

      allCost += sum;
      allBonus += bonus;
      allPrice += sumPrice;

      temp[index] = {
        ...stock,
        totalCost: sum,
        totalPrice: sumPrice,
        qtys: sumQty,
        avgCost: avgCost,
        bonus: bonus,
        bonusColor: bonus > 0 ? 'red' : 'green',
        bonusPercent: bonus / sum,
      };
    });

    setAllStockAmt({
      cost: allCost,
      bonus: allBonus,
      price: allPrice,
      bonusPercent: allBonus / allCost,
    });
    return temp;
  };

  // 
  const stockTransaction = (stockName) => {
    let tempRows = transactionRows.filter((obj) => obj.name == stockName);
    return tempRows;
  };

  /**********  ************/
  // 
  const defaultRow = {
    name: '', // 
    price: '', // 
    qtys: '', //
    totalCost: '', //
    avgCost: '', //
    bouns: '', //
  };
  // 
  const [rows, setRows] = useState([]);
  // 
  const [row, setRow] = useState(defaultRow);
  // 
  const [rowIndex, setRowIndex] = useState(defaultRow);
  // 
  const [open, setOpen] = useState(false);

  const [allStockAmt, setAllStockAmt] = useState({
    totalCost: 0,
    totalBouns: 0,
  });

  /**********  ************/
  const handleAdd = () => {
    setOpen(true);
    setRowIndex(-1);
    setRow(defaultRow);
  };

  // ()
  const handleSave = () => {
    // 
    if (rowIndex == -1) {
      db.collection(colName)
        .add(row)
        .then((docRef) => {
          // 
          setStatRows([...statRows, { ...row, id: docRef.id }]);
        });
    } else {
      // 
      db.collection(colName)
        .doc(row.id)
        .update(row)
        .then(() => {
          const tempRows = statRows.slice();
          Object.assign(tempRows[rowIndex], row);
          setStatRows(tempRows);
          setRowIndex(-1);
          // ,
          if (stockTransaction(row.name).length > 0) {
            setStatRows(cals());
          }
        });
    }

    // 
    setOpen(false);
    // 
    setRow(defaultRow);
  };

  // 
  const handleDelete = () => {
    db.collection(colName)
      .doc(row.id)
      .delete()
      .then(() => {
        setStatRows(statRows.filter((obj) => obj.id != row.id));
        setOpen(false);
      });
  };

  // 
  const handleEdit = (editedRow, index) => {
    setRow(editedRow);
    setRowIndex(index);
    setOpen(true);
  };

  // 
  const handleChange = (e) => {
    setRow({ ...row, [e.target.name]: e.target.value });
  };

  return (
    <>
      <StockPrice />
      <GridView rows={statRows}/>
      <TableView
        rows={statRows}
        setStatRows={setStatRows}
        handleEdit={handleEdit}
        handleAdd={handleAdd}
        allStockAmt={allStockAmt}
        handleRowClick={handleRowClick}
      />

      <EditForm
        open={open}
        setOpen={setOpen}
        row={row}
        handleSave={handleSave}
        handleDelete={handleDelete}
        handleChange={handleChange}
      />
    </>
  );
}
