import React, { useState, useEffect } from 'react';
import schema from './data/schema.json';
import useAsyncReducer from './functions/asyncReducer';
import { reducer } from './data/reducer';
import TableView from './components/TableView';
import EditForm from './components/EditForm';
import { Link } from 'react-router-dom';

export default function index() {
  // 
  const initState = {
    data: [], //
    loading: true, //
    rowIndex: -1, //
    open: false, //
  };

  const [state, dispatch] = useAsyncReducer(reducer, initState);

  useEffect(() => {
    // 
    dispatch({ type: 'LOAD' });
  }, []);

  let { columns } = schema;
  // 
  const defaultRow = {};
  let editableColumns = columns.filter((col) => col.editable);
  editableColumns.map((obj) => {
    defaultRow[obj.name] = '';
  });


  // 
  defaultRow.fromDate = new Date().toISOString().substring(0, 10);

  //  row  useAsyncReducer 
  // 
  const [row, setRow] = useState(defaultRow);

  // 
  const handleAdd = () => {
    dispatch({ type: 'ADD' });
    setRow(defaultRow);
  };

  // 
  const handleEdit = (row, index) => {
    dispatch({ type: 'EDIT', payload: { index } });
    setRow(row);
  };

  return (
    <div>
      <Link to="/stock-detail"></Link>
      <TableView
        columns={columns}
        state={state}
        dispatch={dispatch}
        handleEdit={handleEdit}
        handleAdd={handleAdd}       
      />
      <EditForm
        columns={columns}
        row={row}
        setRow={setRow}
        state={state}
        dispatch={dispatch}
      />
    </div>
  );
}
